  font-weight: 700;
  font-style: normal;
}
.footer__header, .btn, .user-actions, .language-selection, .header__nav {
  display: flex;
  align-items: center;
}

body {
  margin: 0;
  background-color: #131111;
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #F64F00;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #F64F00;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1249px;
  padding: 0 15px;
  margin: 0 auto;
}

/* Page
================*/
.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  overflow: hidden;
}

/* Components */
.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background-color: #131111;
}
.header__nav {
  height: 80px;
}
@media (max-width: 767px) {
  .header__nav {
    height: 70px;
  }
}
@media (max-width: 575px) {
  .header__nav {
    height: 65px;
  }
}
.header__logo {
  margin-right: 40px;
}
@media (max-width: 1199px) {
  .header__logo {
    margin-right: 20px;
  }
}
.header__language {
  margin-right: 90px;
}
@media (max-width: 1199px) {
  .header__language {
    margin-right: 20px;
  }
}
.header__site-nav {
  flex-grow: 1;
  margin-right: 20px;
}
@media (max-width: 1199px) {
  .header__site-nav {
    display: none;
  }
}
@media (max-width: 1199px) {
  .header__burger {
    flex-grow: 1;
    margin-right: 15px;
  }
}

