  height: 27px;
}

.language-selection {
  color: #7D7D7D;
}
.language-selection__item {
  transition: color 0.2s ease;
}
.language-selection__item:first-child {
  margin-right: 4px;
  padding-right: 14px;
  position: relative;
}
.language-selection__item:first-child:before {
  content: "/";
  color: #7D7D7D;
  position: absolute;
  top: 0;
  right: 0;
}
.language-selection__item--active {
  color: #D8D8D8;
}
.language-selection__item:hover {
  color: #F5F5F5;
}

.site-nav {
  display: flex;
  justify-content: center;
}
.site-nav__item {
  color: #7D7D7D;
  transition: color 0.2s ease;
}
.site-nav__item--active {
  font-weight: 600;
  line-height: 20px;
  color: #BCBCBC;
}
.site-nav__item:not(:last-child) {
